import { useState } from "react";

const State = () => {
    const [count, setCount] = useState(0);
    // Increment Function
    const Increment = () => {
        let val = count;
        if (val === 0) {
            document.getElementById('Error').style.opacity = "0";
            setCount(count + 1);
        }
        else {
            setCount(count + 1);
        }
    }
    // Decrement Function
    const Decrement = () => {
        let val = count;
        if (val === 0) {
            document.getElementById('Error').style.opacity = "1";
        }
        else {
            setCount(count - 1);
        }
    }
    // Clear function
    const Clear = () => {
        setCount(0);
    }
    return (
        <div className="Center_Div">
            <div id="Color" className="Child__Center">
                <h1>{count}</h1>
                <button className="Btn" onClick={Increment}>+</button>
                <button className="Btn" onClick={Decrement}>âˆ’</button>
                <div>
                    <button className="clear" onClick={Clear}>C</button>
                </div>
                <div className="Err">
                    <p id="Error"><span>The value Cannot Be lesser than 0</span></p>
                </div>
            </div>
        </div >
    );
}

export default State;